.display-flex{
    display: flex;
}
.pending-container{
    margin-left: 20.059vw;
    margin-right: 20px;
    width: 100%;
    margin-top: 7.291666666666667vh;
}

.form-fiter{
    display: flex;
    background: #FFFFFF;
    border: 1px solid #E5E5E5;
    box-sizing: border-box;
    border-radius: 10px;
    padding: 30px;
    margin-top: 3vh;
    width: 77.965vw;

}
.modalLoading {
    backdrop-filter: blur(5px);
    position: fixed;
    top:0vh;
    z-index: 99;
    width: 100vw;
    height: 100vh;
    background:  rgba(255, 255, 255, 0.4) url("../../assets/canguro-pickers.gif") 50% 50% no-repeat;
}
.amountTotalPositive{
    display: inline;
    color: rgb(101, 168, 0);
}
.amountTotalNegative{
    display: inline;
    color: #ff1515;
}

@media (min-width:1800px){
    .pending-container{
        margin-left: 17.059vw;
    }
}